// @flow

import * as React from "react";
import axios from 'axios';

import {
  Page,
  Avatar,
  Icon,
  Grid,
  Card,
  Text,
  Table,
  Alert,
  Progress,
  colors,
  Dropdown,
  Button,
  StampCard,
  StatsCard,
  ProgressCard,
  Badge,
  Form,
  BlogCard,
} from "tabler-react";

import C3Chart from "react-c3js";

import SiteWrapper from "./SiteWrapper.react";

class Home extends React.Component{

  constructor(props){
    super(props);
    this.state = {
     
        patchList : [] //  .
    };
}

componentDidMount(){
    this._getList();
}


_getList(){
    //webtoon_list  .
    const apiUrl = '/demo/dummy/patch_list.json';

    axios.get(apiUrl)
        .then(data => {
            //   state .
            this.setState({
                patchList : data.data.patchList
                
            });
        }
        )
        .catch(error => {
            console.log(error);
        });
}

render(){


  return (
    <SiteWrapper>
         <Page.Content title="" >
          <div className="page-single">
           <img src="./demo/brand/mylog.svg"></img>
           </div>
          <div className="page-single">
            <Grid.Col lg={6} >
              <Form.Group label="">
              <Form.InputGroup>
                <Form.Input placeholder=" ." />
                <Form.InputGroupAppend>
                  <Button
                    RootComponent="a"
                    color="primary"
                    href="/profile"
                  >
                    GG
                  </Button>
                </Form.InputGroupAppend>
              </Form.InputGroup>
            </Form.Group>
            </Grid.Col>
            </div>
       </Page.Content>
      <Page.Content title="">
      <Grid.Row cards deck>

      {this.state.patchList.map((item, key) => (

          <Grid.Col sm={6} xl={3} key={key}>
            <BlogCard
              imgSrc={item.imgSrc}
              imgAlt={item.imgAlt}
              postHref={item.postHref}
              title={item.title}
              description={
                ""
              }
              profileHref={""}
              authorName={item.authorName}
              avatarImgSrc={item.avatarImgSrc}
              date={item.date}
            />
          </Grid.Col>
        ))}
      </Grid.Row>
      </Page.Content>
    </SiteWrapper>
  );
}
}

export default Home;
